import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "react-phone-number-input/style.css";
import { isValidPhoneNumber } from "react-phone-number-input";
import PhoneInput from "react-phone-input-2";
import Modal from "../../components/Modal/Modal";
import "react-phone-input-2/lib/material.css";
import {
  Avatar,
  Grid,
  Paper,
  TextField,
  Button,
  Typography,
  FormControl,
  IconButton,
  OutlinedInput,
  InputAdornment,
  InputLabel,
  FormHelperText,
  FormControlLabel,
  Checkbox,
} from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import { pink } from "@material-ui/core/colors";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import LockOpenOutlinedIcon from "@material-ui/icons/LockOpenOutlined";
import "./SignUp.css";
const useStyles = makeStyles((theme) => ({
  padding: {
    padding: theme.spacing(2),
  },
  withoutLabel: {
    marginBottom: theme.spacing(2),
  },
  pink: {
    color: theme.palette.getContrastText(pink[500]),
    backgroundColor: pink[500],
  },
}));

const UserSignUp = ({ firebase, history }) => {
  const classes = useStyles();
  const [signingIn, setSigningIn] = useState(true);
  const [disabled, setDisabled] = useState(true);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [number, setNumber] = useState("");
  const [error, setError] = useState(false);
  const [emailError, setEmailError] = useState(false);
  const [passwordError, setPasswordError] = useState(false);
  const [nameError, setNameError] = useState(false);
  // const [loggedIn, setLoggedIn] = useState(false);
  const [check, setCheck] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  // useEffect(() => {
  //   if (!!firebase.user) {
  //     history.push("/home");
  //   }
  // }, []);

  useEffect(() => {
    if (
      !!email &&
      !!password &&
      !!confirmPassword &&
      !!firstName &&
      !!lastName && !!check
    ) {
      setDisabled(false);
    } else {
      setDisabled(true);
    }
  }, [email, password, confirmPassword, firstName, lastName,check]);

  const handleSignUpButton = async () => {
    if (firstName.length === 0) {
      setPasswordError("");
      setError("");
      return setNameError("Please enter valid name");
    }
    if (lastName.length === 0) {
      return setNameError("Please enter valid name");
    }
    if (!new RegExp(/[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,15}/g).test(email)) {
      setNameError("");
      setPasswordError("");
      setError("");
      return setEmailError("Enter valid email");
    }
    if (password.length < 6) {
      setEmailError("");
      setNameError("");
      setError("");
      setPasswordError("Password must be at least six characters");
      return;
    }
    if (password !== confirmPassword) {
      return setPasswordError("Password do not match.");
    }
    if (!isValidPhoneNumber("+" + number)) {
      setPasswordError("");
      setEmailError("");
      setNameError("");
      return setError("Please enter a valid number");
    }

    setDisabled(true);
    setSigningIn(true);
    // const res = await firebase.createUser(email, password);
    const newRecord = {
      fName: firstName,
      lName: lastName,
      phone: "+" + number,
      email: email,
      password: password,
      check: check
    };
    console.log("new record", newRecord)
    // console.log("res from fb instance", res);
    // const { success, error } = res;
    // if (success) {
    //   firebase.addUserRecord(newRecord);
    //   history.push("/");
    // } else {
    //   setError(error);
    //   setSigningIn(false);
    //   setDisabled(false);
    // }
  };

  return (
    <div className='signupGrid'>
      <Grid item lg={5}>
        <Paper
          elevation={10}
          style={{ padding: "20px" }}
          className='signupForm'
        >
          <Grid align='center'>
            <Avatar className={classes.pink}>
              <LockOpenOutlinedIcon />
            </Avatar>
            <h1>Register</h1>
            <p>Please complete to create your account</p>
          </Grid>
          {error.length>0 && (
            <Alert className={clsx(classes.withoutLabel)} variant='filled' severity='error'>
              {error}
            </Alert>
          )}
          <Grid container justify='space-between'>
            <FormControl id='signUpName' className={clsx(classes.withoutLabel)}>
              <TextField
                error={nameError.length>0}
                helperText={nameError}
                autoFocus
                id='outlined-first-name'
                label='Enter First Name'
                type='text'
                variant='outlined'
                placeholder='First Name'
                value={firstName}
                onChange={(e) => setFirstName(e.target.value)}
                required
              />
            </FormControl>
            <FormControl id='signUpName' className={clsx(classes.withoutLabel)}>
              <TextField
                error={nameError.length>0}
                helperText={nameError}
                id='outlined-last-name'
                label='Enter Last Name'
                type='text'
                variant='outlined'
                placeholder='Last Name'
                value={lastName}
                onChange={(e) => setLastName(e.target.value)}
                required
              />
            </FormControl>
          </Grid>
          <Grid container justify='space-between'>
            <FormControl className={clsx(classes.withoutLabel)}>
              <TextField
                error={emailError.length>0}
                helperText={emailError}
                id='outlined-email'
                label='Enter Email'
                type='email'
                variant='outlined'
                placeholder='example@mail.com'
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </FormControl>
            <Modal header='Verify your Email' />
          </Grid>

          <Grid container justify='space-between'>
            <FormControl className={clsx(classes.withoutLabel)}>
              <PhoneInput
                enableSearch
                autocompleteSearch
                country={"in"}
                value={number}
                onChange={setNumber}
                required
              />
            </FormControl>
            <Modal header='Verify your Number' />
          </Grid>
          <FormControl
            className={clsx(classes.withoutLabel)}
            variant='outlined'
            required
            fullWidth
          >
            <InputLabel htmlFor='outlined-adornment-password'>
              Password
            </InputLabel>

            <OutlinedInput
              error={passwordError.length>0}
              id='outlined-adornment-password'
              placeholder='Enter Password'
              type={showPassword ? "text" : "password"}
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              endAdornment={
                <InputAdornment position='end'>
                  <IconButton
                    aria-label='toggle password visibility'
                    onClick={() => setShowPassword(!showPassword)}
                    edge='end'
                  >
                    {showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              }
              labelWidth={80}
            />
            {passwordError.length>0 && (
              <FormHelperText style={{ color: "red" }}>
                {passwordError}
              </FormHelperText>
            )}
          </FormControl>

          <FormControl
            className={clsx(classes.withoutLabel)}
            variant='outlined'
            required
            fullWidth
          >
            <InputLabel htmlFor='outlined-adornment-confirm-password'>
              Confirm Password
            </InputLabel>

            <OutlinedInput
              error={passwordError.length>0}
              id='outlined-adornment-confirm-password'
              placeholder='Enter Password'
              type={showPassword ? "text" : "password"}
              value={confirmPassword}
              onChange={(e) => setConfirmPassword(e.target.value)}
              labelWidth={150}
            />
            {passwordError.length>0 && (
              <FormHelperText style={{ color: "red" }}>
                {passwordError}
              </FormHelperText>
            )}
          </FormControl>
          <FormControlLabel
            control={
              <Checkbox
                checked={check}
                onChange={() => setCheck(!check)}
                name='rememberMe'
              />
            }
            label='I agree with terms and conditions'
          />
          <FormControl className={clsx(classes.withoutLabel)} fullWidth>
            {signingIn && (
              <Button
                type='submit'
                variant='contained'
                color='primary'
                disabled={disabled}
                onClick={handleSignUpButton}
              >
                Create Account
              </Button>
            )}
          </FormControl>

          <Typography align='right'>
            Already have an Account ? <Link to='/login'>Login</Link>
          </Typography>
        </Paper>
      </Grid>
    </div>
  );
};
export default UserSignUp;
