import React, { useState, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Backdrop from "@material-ui/core/Backdrop";
import Fade from "@material-ui/core/Fade";
import clsx from "clsx";
import { FormControl, Button, TextField, Grid } from "@material-ui/core";
const useStyles = makeStyles((theme) => ({
  modal: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    borderRadius: "5px",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  withoutLabel: {
    marginBottom: theme.spacing(2),
  },
}));

export default function TransitionsModal({ header }) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [value, setValue] = useState("");
  const [error, setError] = useState("");
  const [counter, setCounter] = useState(300);
  const [disabled, setDisabled] = useState(true);

  useEffect(() => {
    counter > 0 && setTimeout(() => setCounter(counter - 1), 1000);
  }, [counter]);
  //   check OTP length to toggle verify button visibility
  useEffect(() => {
    if (value.length >= 6) {
      setDisabled(false);
    } else {
      setDisabled(true);
    }
  }, [value]);
  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
    setCounter(0);
  };
  const handleVerify = () => {
    console.log("value", value);
    if (value !== "api value") {
      setDisabled(true);
      setError("Enter Valid OTP");
    } else {
      setError("");
      handleClose();
    }
  };
  const handleResendOTP = () => {};
  return (
    <div>
      <Button
        disabled={open}
        variant='contained'
        color='primary'
        onClick={handleOpen}
      >
        Send OTP
      </Button>
      <Modal
        aria-labelledby='transition-modal-title'
        aria-describedby='transition-modal-description'
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div elevation={10} className={classes.paper}>
            <h2 className={clsx(classes.withoutLabel)}>{header}</h2>
            <Grid container justify='space-between'>
              <FormControl
                id='signUpName'
                className={clsx(classes.withoutLabel)}
                fullWidth
              >
                <TextField
                  autoComplete='off'
                  error={error}
                  helperText={error}
                  autoFocus
                  id='outlined-secondary'
                  label='Enter OTP'
                  type='text'
                  variant='outlined'
                  placeholder='OTP'
                  value={value}
                  onChange={(e) => setValue(e.target.value)}
                  required
                />
              </FormControl>
              <Button
                disabled={disabled}
                variant='contained'
                color='primary'
                onClick={handleVerify}
              >
                Verify
              </Button>
              {counter > 0 ? (
                `${parseInt(counter / 60)} : ${counter % 60}`
              ) : (
                <Button
                  variant='contained'
                  color='primary'
                  onClick={handleResendOTP}
                >
                  Resend OTP
                </Button>
              )}
            </Grid>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}
